  cursor: pointer;
  text-decoration: none !important;
  outline: none;
  min-width: 100px;
  border-radius: 5px;
  -webkit-tap-highlight-color: transparent;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.btn:active {
    opacity: 0.6;
}
.btn-round {
    border-radius: 42px;
}
.btn.disable {
    opacity: .6;
}
